.mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
}
.mask .toast-msg {
  font-size: 3.2vw;
  text-align: center;
  position: relative;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #fff;
  left: 50%;
  padding: 2.4vw 3.6vw;
  overflow: hidden;
  border-radius: 0.533vw;
  white-space: nowrap;
  display: block;
  background: rgba(0, 0, 0, 0.7);
  content: 'viewport-units-buggyfill; font-size: 3.2vw; padding: 2.4vw 3.6vw; border-radius: 0.533vw';
}
.mask .toast {
  font-size: 0px;
  padding: 3.6vw 1.2vw;
  width: 30.133vw;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #f2f2f2;
  background: rgba(51, 51, 51, 0.94);
  border-radius: 1.2vw;
  text-align: center;
  content: 'viewport-units-buggyfill; padding: 3.6vw 1.2vw; width: 30.133vw; border-radius: 1.2vw';
}
.mask .toast .icon {
  width: 9.6vw;
  height: 9.6vw;
  border-radius: 50%;
  border: 1px solid #f2f2f2;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  margin-bottom: 2.4vw;
  content: 'viewport-units-buggyfill; width: 9.6vw; height: 9.6vw; margin-bottom: 2.4vw';
}
.mask .toast .icon .success-icon {
  border-right: 1px solid #f2f2f2;
  border-bottom: 1px solid #f2f2f2;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 3.6vw;
  height: 6vw;
  margin-top: -2.4vw;
  content: 'viewport-units-buggyfill; width: 3.6vw; height: 6vw; margin-top: -2.4vw';
}
.mask .toast .icon .fail-icon:before {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  border-top: 1px solid #f2f2f2;
  width: 6.667vw;
  height: 0px;
}
.mask .toast .icon .fail-icon:after {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
  border-top: 1px solid #f2f2f2;
  width: 6.667vw;
  height: 0px;
}
.mask .toast .icon .warning-icon:before {
  content: "";
  display: block;
  position: absolute;
  top: 2vw;
  left: 50%;
  background: #f2f2f2;
  width: 1px;
  height: 4vw;
}
.mask .toast .icon .warning-icon:after {
  content: "";
  position: absolute;
  bottom: 1.867vw;
  left: 50%;
  background: #f2f2f2;
  width: 0.8vw;
  margin-left: -0.267vw;
  border-radius: 50%;
  height: 0.8vw;
}
.mask .toast .msg {
  font-size: 3.2vw;
  content: 'viewport-units-buggyfill; font-size: 3.2vw';
}
.loading-icon {
  font-size: 0px;
  margin-bottom: 2.4vw;
  box-sizing: border-box;
  width: 9.6vw;
  height: 9.6vw;
  position: relative;
  content: 'viewport-units-buggyfill; margin-bottom: 2.4vw; width: 9.6vw; height: 9.6vw';
}
.loading-icon span {
  position: absolute;
  height: 1px;
  left: 50%;
  top: 50%;
  width: 2.4vw;
  -webkit-animation: loading-fade-light 1.1s infinite linear;
          animation: loading-fade-light 1.1s infinite linear;
  background: rgba(255, 255, 255, 0.3);
  -webkit-transform-origin: -2.4vw 50%;
          transform-origin: -2.4vw 50%;
  margin-left: 2.4vw;
  content: 'viewport-units-buggyfill; width: 2.4vw; -webkit-transform-origin: -2.4vw 50%; transform-origin: -2.4vw 50%; margin-left: 2.4vw';
}
.loading-icon span:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.loading-icon span:nth-child(2) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.loading-icon span:nth-child(3) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}
.loading-icon span:nth-child(4) {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.loading-icon span:nth-child(5) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
}
.loading-icon span:nth-child(6) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
}
.loading-icon span:nth-child(7) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.loading-icon span:nth-child(8) {
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
}
.loading-icon span:nth-child(9) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
}
.loading-icon span:nth-child(10) {
  -webkit-animation-delay: 0.9s;
          animation-delay: 0.9s;
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.loading-icon span:nth-child(11) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
}
.loading-icon span:nth-child(12) {
  -webkit-animation-delay: 1.1s;
          animation-delay: 1.1s;
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
}
@-webkit-keyframes loading-fade-light {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: rgba(255, 255, 255, 0);
  }
}
@keyframes loading-fade-light {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: rgba(255, 255, 255, 0);
  }
}
