.App {
  height: 100%;
  min-height: 100%;
  text-align: center;
}
.App button {
  width: 40vw;
  height: 6.667vw;
  border: 1px solid red;
  margin: 2.667vw auto;
  content: 'viewport-units-buggyfill; width: 40vw; height: 6.667vw; margin: 2.667vw auto';
}
.App-header {
  background-color: #282c34;
  display: flex;
  height: 46.667vw;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  content: 'viewport-units-buggyfill; height: 46.667vw';
}
.App-header .App-logo2 {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
  height: 21.333vw;
  pointer-events: none;
  content: 'viewport-units-buggyfill; height: 21.333vw';
}
.App-header .title {
  margin-top: 4vw;
  line-height: 5.867vw;
  content: 'viewport-units-buggyfill; margin-top: 4vw; line-height: 5.867vw';
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
