.mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  left: 0px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}
.mask .alert {
  width: 72.667vw;
  overflow: hidden;
  background: #fcfcfc;
  border-radius: 1.2vw;
  content: 'viewport-units-buggyfill; width: 72.667vw; border-radius: 1.2vw';
}
.mask .alert .title1 {
  font-size: 4.8vw;
  padding: 4.4vw 0rem;
  color: #333333;
  text-align: center;
  content: 'viewport-units-buggyfill; font-size: 4.8vw; padding: 4.4vw 0rem';
}
.mask .alert .title2 {
  font-size: 3.733vw;
  padding: 0px 0px 4.8vw;
  color: #797979;
  text-align: center;
  content: 'viewport-units-buggyfill; font-size: 3.733vw; padding: 0px 0px 4.8vw';
}
.mask .alert .btnlist {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.4vw;
  height: 10.933vw;
  position: relative;
  font-size: 3.733vw;
  content: 'viewport-units-buggyfill; font-size: 0.4vw; height: 10.933vw; font-size: 3.733vw';
}
.mask .alert .btnlist .ok {
  flex: 1 1;
  height: 10.933vw;
  line-height: 10.933vw;
  text-align: center;
  color: #636363;
  content: 'viewport-units-buggyfill; height: 10.933vw; line-height: 10.933vw';
}
.mask .alert .btnlist .abort {
  position: relative;
  flex: 1 1;
  line-height: 10.933vw;
  height: 10.933vw;
  text-align: center;
  color: #FF8200;
  content: 'viewport-units-buggyfill; line-height: 10.933vw; height: 10.933vw';
}
